// ===============================================
// TYPOGRAPHY VARIABLES
// ===============================================


// stylelint-disable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


// FONT FAMILIES
// -----------------------------------------------

--ff-nunito-sans: 'Nunito Sans', 'Helvetica Neue', 'Arial', sans-serif;

// -----------------------------------------------
// FONT FAMILIES


// FONT WEIGHTS
// -----------------------------------------------

--fw-xl: 200;
--fw-l: 300;
--fw-n: 400;
--fw-m: 500;
--fw-sb: 600;
--fw-b: 700;
--fw-xb: 800;

--h1-fw: var(--fw-b);
--h2-fw: var(--fw-b);
--h3-fw: var(--fw-b);
--h4-fw: var(--fw-b);
--h5-fw: var(--fw-b);

// -----------------------------------------------
// FONT WEIGHTS


// FONT SIZES
// -----------------------------------------------

--fz-xs: 14px;
--fz-sm: 16px;
--fz-md: 18px;


--df_h1-fz: 52px;
--md_h1-fz: 64px;
--xl_h1-fz: 80px;

--df_h2-fz: 20px;
--md_h2-fz: 32px;
--xl_h2-fz: 42px;

--df_h3-fz: 18px;
--md_h3-fz: 28px;
--xl_h3-fz: 36px;

--df_h4-fz: 16px;
--md_h4-fz: 20px;
--xl_h4-fz: 28px;

--df_h5-fz: 16px;
--md_h5-fz: 18px;

// -----------------------------------------------
// FONT SIZES


// LINE HEIGHTS
// -----------------------------------------------

--lh-1: 1;
--lh-xs: 1.25;
--lh-sm: 1.27;
--lh-md: 1.33;
--lh-lg: 1.43;
--lh-xl: 1.5;
--lh-xxl: 1.8;

// -----------------------------------------------
// LINE HEIGHTS


// UNORDERED LIST
// -----------------------------------------------

--ul-dashed-dash-t: -.1em;
--ul-dashed-dash-pr: .75em;

--ul-point-dot-t: -.16em;
--ul-point-dot-pr: .9em;
--ul-point-dot-fz: .8em;
--ul-point-dot-lh: 1em;
--ul-point-dot-cl: var(--cl-violet-200);

// -----------------------------------------------
// UNORDERED LIST


// ORDERED LIST
// -----------------------------------------------

--ol-number-ff: var(--ff-roboto);
--ol-number-default-pr: .25em;
--ol-number-flat-pr: 0;
--ol-number-ls: normal;

// -----------------------------------------------
// ORDERED LIST


// HORIZONTAL SEPARATOR
// -----------------------------------------------

--hr-default-bg-cl: var(--cl-gray-600);

--hr-my: 16px;
--hr-height: 1px;

// -----------------------------------------------
// HORIZONTAL SEPARATOR


}
// stylelint-enable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
