// ===============================================
// TYPOGRAPHY
// ===============================================


// HEADINGS
// -----------------------------------------------

.h1,
.h2,
.h3,
.h4 {
	margin-top: 0;
	margin-bottom: 0;
}

.h2 {
	scroll-margin-top: 90px;
}

// -----------------------------------------------
// HEADINGS


// TEXT COMPONENTS
// -----------------------------------------------

@each $component in $text-components {
	.#{$component} {
		@include create-text-properties($component, md);
	}
}

@each $heading in $headings {
	.#{$heading} {
		@include create-text-properties($heading, [md, xl]);
	}
}

// -----------------------------------------------
// TEXT COMPONENTS


// LISTS
// -----------------------------------------------

.ol,
.ul {
	margin: 0;
	padding: 0;
	list-style-position: inside;
}

.ul {
	&--dashed {
		list-style: none;

		> li {
			display: table;
			margin: .5em 0;

			&::before {
				content: '\2014';
				position: relative;
				top: var(--ul-dashed-dash-t);
				display: table-cell;
				padding-right: var(--ul-dashed-dash-pr);
			}
		}
	}

	&--point {
		list-style: none;

		> li {
			display: table;
			margin: .5em 0;

			&::before {
				content: '\2022';
				position: relative;
				top: var(--ul-point-dot-t);
				display: table-cell;
				padding-right: var(--ul-point-dot-pr);
				color: var(--ul-point-dot-cl);
				font-size: var(--ul-point-dot-fz);
				line-height: var(--ul-point-dot-lh);
			}
		}
	}
}

.ol {
	--ol-number-pr: var(--ol-number-default-pr);

	counter-reset: item;
	list-style: none;

	> li {
		counter-increment: item;
		display: table;
		margin: .5em 0;

		&::before {
			content: counter(item) '. ';
			display: table-cell;
			padding-right: var(--ol-number-pr);
			font-family: var(--ol-number-ff);
			letter-spacing: var(--ol-number-ls);
			word-break: normal;
		}
	}

	&--nested {
		counter-reset: item;

		> li {
			&::before {
				content: counters(item, '.') '. ';
			}
		}
	}

	&--flat {
		--ol-number-pr: var(--ol-number-flat-pr);

		> li {
			display: list-item;

			&::before {
				display: inline;
			}
		}
	}
}

// -----------------------------------------------
// LISTS


// CODE
// -----------------------------------------------

.code {
	padding: 2px 6px;
	border: 1px solid var(--cl-gray-600);
	border-radius: 4px;
	background-color: var(--cl-gray-800);
}

// -----------------------------------------------
// CODE


// HORIZONTAL SEPARATOR
// -----------------------------------------------

.hr {
	--hr-bg-cl: var(--hr-default-bg-cl);

	height: var(--hr-height);
	margin-top: var(--hr-my);
	margin-bottom: var(--hr-my);
	background-color: var(--hr-bg-cl);
}

