// ===============================================
// TEXT VARIABLES
// ===============================================


// stylelint-disable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


// TEXT COMPONENTS
// -----------------------------------------------

--df_text-xs-fz: var(--fz-xs);
--md_text-xs-fz: var(--fz-xs);

--df_text-sm-fz: var(--fz-sm);
--md_text-sm-fz: var(--fz-sm);

--df_text-md-fz: var(--fz-sm);
--md_text-md-fz: var(--fz-md);

--text-xs-lh: var(--lh-lg);
--text-sm-lh: var(--lh-xl);
--text-md-lh: var(--lh-md);


--h1-lh: var(--lh-1);
--h2-lh: var(--lh-xs);
--h3-lh: var(--lh-sm);
--h4-lh: var(--lh-lg);
--h5-lh: var(--lh-xs);

// -----------------------------------------------
// TEXT COMPONENTS


// TEXT COLORS
// -----------------------------------------------

--text-white-cl: var(--cl-white);
--text-black-cl: var(--cl-black);
--text-light-cl: var(--cl-gray-300);
--text-dark-cl: var(--cl-gray-100);
--text-primary-cl: var(--cl-violet-100);
--text-success-cl: var(--cl-green-100);
--text-warning-cl: var(--cl-orange-100);
--text-danger-cl: var(--cl-red-200);
--text-info-cl: var(--cl-yellow-100);
--text-muted-cl: var(--cl-gray-400);

// -----------------------------------------------
// TEXT COLORS


}
// stylelint-enable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
