// ===============================================
// LINK VARIABLES
// ===============================================


// stylelint-disable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


--link-decoration: none;
--link-bg: transparent;

--link-default-cl: var(--cl-blue-200);
--link-default-hover-cl: var(--cl-blue-300);
--link-default-active-cl: var(--cl-blue-100);

--link-violet-cl: var(--cl-violet-400);
--link-violet-hover-cl: var(--cl-violet-500);
--link-violet-active-cl: var(--cl-violet-200);


}
// stylelint-enable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
