// ===============================================
// BUTTON VARIABLES
// ===============================================


// stylelint-disable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


// COMMON STYLES
// -----------------------------------------------

--btn-ff: var(--ff-nunito-sans);
--btn-fw: var(--fw-b);

// -----------------------------------------------
// COMMON STYLES


// DEFAULT BUTTON
// -----------------------------------------------

--btn-default-py: 9px;
--btn-default-px: 16px;
--btn-default-fz: var(--fz-sm);
--btn-default-cl: var(--cl-black);
--btn-default-brs: 8px;
--btn-default-bg-cl: transparent;
--btn-default-border-w: 0;
--btn-default-border-cl: transparent;

// -----------------------------------------------
// DEFAULT BUTTON


// PRIMARY BUTTON
// -----------------------------------------------

--btn-primary-cl: var(--cl-white);
--btn-primary-bg-cl: var(--cl-violet-200);

--btn-primary-hov-bg-cl: var(--cl-violet-300);

--btn-primary-active-bg-cl: var(--cl-violet-200);

--btn-primary-disabled-bg-cl: var(--cl-violet-500);

// -----------------------------------------------
// PRIMARY BUTTON


// DANGER BUTTON
// -----------------------------------------------

--btn-danger-cl: var(--cl-white);
--btn-danger-bg-cl: var(--cl-red-200);
--btn-danger-border-cl: var(--cl-red-200);

--btn-danger-hov-border-cl: var(--cl-red-600);

--btn-danger-active-bg-cl: var(--cl-red-100);
--btn-danger-active-border-cl: var(--cl-red-100);

--btn-danger-disabled-bg-cl: var(--cl-red-600);
--btn-danger-disabled-border-cl: var(--cl-red-600);

// -----------------------------------------------
// DANGER BUTTON


// GHOST BUTTON
// -----------------------------------------------

--btn-ghost-cl: var(--cl-black);
--btn-ghost-bg-cl: var(--ca-gray-400-20);
--btn-ghost-border-cl: transparent;

--btn-ghost-hov-bg-cl: var(--ca-gray-400-30);

--btn-ghost-active-bg-cl: var(--ca-gray-400-40);

--btn-ghost-disabled-cl: var(--cl-gray-400);
--btn-ghost-disabled-bg-cl: var(--ca-gray-400-20);

// -----------------------------------------------
// GHOST BUTTON


// SM SIZE
// -----------------------------------------------

--btn-sm-fz: 12px;
--btn-sm-py: 6px;
--btn-sm-px: 12px;

// -----------------------------------------------
// SM SIZE


// XS SIZE
// -----------------------------------------------

--btn-xs-fz: 9px;
--btn-xs-py: 2px;
--btn-xs-px: 8px;
--btn-xs-brs: 6px;

// -----------------------------------------------
// XS SIZE


// LG SIZE
// -----------------------------------------------

--btn-lg-fz: 20px;
--btn-lg-py: 16px;
--btn-lg-px: 32px;
--btn-lg-brs: 8px;

// -----------------------------------------------
// LG SIZE


}
// stylelint-enable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
