// ===============================================
// REBOOT STYLES
// ===============================================


/*
	Change from `box-sizing: content-box` so that `width` is not affected
	by `padding` or `border`.
*/
html {
	box-sizing: border-box;
}

// stylelint-disable scss/selector-nest-combinators

*,
*::after,
*::before {
	box-sizing: inherit;
}

// stylelint-enable scss/selector-nest-combinators


// ===============================================
// NORMALIZE
// ===============================================


/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */
/*! modern-normalize v1.0.0 | MIT License | https://github.com/sindresorhus/modern-normalize */


// DOCUMENT
// -----------------------------------------------

// Use a more readable tab size (opinionated).
:root {
	-moz-tab-size: 4;
	tab-size: 4;
}

/*
	1. Correct the line height in all browsers.
	2. Prevent adjustments of font size after orientation changes in iOS.
*/
// stylelint-disable-next-line no-duplicate-selectors
html {
	line-height: 1.15; /* 1 */
	-webkit-text-size-adjust: 100%; /* 2 */
}

// -----------------------------------------------
// DOCUMENT


// SECTIONS
// -----------------------------------------------


body {
	// Remove the margin in all browsers.
	margin: 0;

	/*
		Improve consistency of default fonts in all browsers.
		(https://github.com/sindresorhus/modern-normalize/issues/3)
	*/
	font-family:
		system-ui,
		-apple-system, /* Firefox supports this but not yet `system-ui` */
		'Segoe UI',
		'Roboto',
		'Helvetica',
		'Arial',
		sans-serif,
		'Apple Color Emoji',
		'Segoe UI Emoji';
}

/*
	Correct the font size and margin on `h1` elements within `section`
	and `article` contexts in Chrome, Firefox, and Safari.
*/
h1 {
	margin: .67em 0;
	font-size: 2em;
}

// -----------------------------------------------
// SECTIONS


// CONTENT GROUPING
// -----------------------------------------------

/*
	1. Add the correct height in Firefox.
	2. Correct the inheritance of border color in Firefox.
	(https://bugzilla.mozilla.org/show_bug.cgi?id=190655)
*/
hr {
	height: 0; /* 1 */
	border: 0;
	color: inherit; /* 2 */
}

// -----------------------------------------------
// CONTENT GROUPING


// TEXT-LEVEL SEMANTICS
// -----------------------------------------------

// Add the correct font weight in Chrome, Edge, and Safari.
b,
strong {
	// stylelint-disable-next-line font-weight-notation
	font-weight: bolder;
}

/*
	1. Improve consistency of default fonts in all browsers.
	(https://github.com/sindresorhus/modern-normalize/issues/3)
	2. Correct the odd 'em' font sizing in all browsers.
*/
code,
kbd,
samp,
pre {
	font-family:
		'ui-monospace',
		'SFMono-Regular',
		'Consolas',
		'Liberation Mono',
		'Menlo',
		monospace; /* 1 */

	font-size: 1em; /* 2 */
}

p {
	margin-top: 0;
	margin-bottom: 0;
}

// -----------------------------------------------
// TEXT-LEVEL SEMANTICS


// TABULAR DATA
// -----------------------------------------------

/*
	1. Correct table border color inheritance in all Chrome and Safari.
	(https://bugs.chromium.org/p/chromium/issues/detail?id=935729, https://bugs.webkit.org/show_bug.cgi?id=195016)
	2. Remove text indentation from table contents in Chrome and Safari.
	(https://bugs.chromium.org/p/chromium/issues/detail?id=999088, https://bugs.webkit.org/show_bug.cgi?id=201297)
*/
table {
	border-color: inherit; /* 1 */
	text-indent: 0; /* 2 */
}

// -----------------------------------------------
// TABULAR DATA


// FORMS
// -----------------------------------------------

/*
	1. Remove the margin in Firefox and Safari.
	2. Change the font styles in all browsers.
*/
button,
input,
optgroup,
select,
textarea {
	margin: 0; /* 1 */
	font-family: inherit; /* 2 */
	font-size: 100%; /* 2 */
	line-height: 1.15; /* 2 */
}

/*
	Remove the inheritance of text transform in Edge, Firefox, and IE.
	1. Remove the inheritance of text transform in Firefox.
*/
button,
select { /* 1 */
	text-transform: none;
}

// Correct the inability to style clickable types in iOS and Safari.
button,
[type='button'],
[type='reset'],
[type='submit'] {
	-webkit-appearance: button;
}

// Remove the inner border and padding in Firefox.
::-moz-focus-inner {
	padding: 0;
	border-style: none;
}

// Restore the focus styles unset by the previous rule.
:-moz-focusring {
	outline: 1px dotted currentColor;
}

/*
	Remove the additional ':invalid' styles in Firefox.
	See: https://github.com/mozilla/gecko-dev/blob/2f9eacd9d3d995c937b4251a5557d95d494c9be1/layout/style/res/forms.css#L728-L737
*/
:-moz-ui-invalid {
	box-shadow: none;
}

// -----------------------------------------------
// FORMS


// ===============================================
// BOOTSTRAP REBOOT
// ===============================================


/*! twbs/bootstrap v5.0.0 | MIT License | https://github.com/twbs/bootstrap */


// TEXT-LEVEL SEMANTICS
// -----------------------------------------------

pre {
	// Don't allow content to break outside
	overflow: auto;

	/*
		Disable auto-hiding scrollbar in IE & legacy Edge to avoid overlap,
		making it impossible to interact with the content
	*/
	-ms-overflow-style: scrollbar;
}

// -----------------------------------------------
// TEXT-LEVEL SEMANTICS


// FORMS
// -----------------------------------------------

/*
	Work around a Firefox/IE bug where the transparent `button` background
	results in a loss of the default `button` focus styles.

	Credit: https://github.com/suitcss/base/
*/
button {
	&:focus {
		outline: 0;
	}
}

/*
	Explicitly remove focus outline in Chromium when it shouldn't be
	visible (e.g. as result of mouse click or touch tap). It already
	should be doing this automatically, but seems to currently be
	confused and applies its very visible two-tone outline anyway.
*/
// stylelint-disable-next-line scss/selector-nest-combinators
button:focus:not(:focus-visible) {
	outline: 0;
}

// -----------------------------------------------
// FORMS
