// ===============================================
// FOOTER
// ===============================================


.footer {
	--footer-link-cl: var(--footer-link-default-cl);

	&__container {
		@include media-breakpoint-up(sm) {
			padding-top: 40px;
		}

		padding-top: 52px;
		padding-bottom: 60px;
	}

	&__navigation {
		@include media-breakpoint-up(sm) {
			margin-top: 8px;
		}
	}

	&__menu {
		margin-top: 16px;
		margin-bottom: 0;
		padding-left: 0;
		list-style: none;
	}

	&__menu-item {
		margin-top: 16px;
	}

	&__menu-heading {
		margin-top: 0;
		margin-bottom: 0;
		font-size: var(--footer-menu-heading-fz);
		line-height: 1.2;
	}

	&__link {
		margin-top: 16px;
		color: var(--footer-link-cl);
		font-size: var(--footer-link-fz);
		text-decoration: none;
		transition: color .2s ease;

		&:hover {
			--footer-link-cl: var(--footer-link-hover-cl);
		}
	}
}
