// ===============================================
// DROPDOWN VARIABLES
// ===============================================


// stylelint-disable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


// TOGGLE
// -----------------------------------------------

--dropdown-toggle-cl: var(--cl-gray-100);
--dropdown-toggle-fz: var(--fz-xs);

--dropdown-toggle-default-bg-cl: var(--ca-gray-400-20);
--dropdown-toggle-hover-bg-cl: var(--ca-gray-400-30);
--dropdown-toggle-active-bg-cl: var(--ca-gray-400-40);

--dropdown-link-fz: var(--fz-xs);

// -----------------------------------------------
// TOGGLE


// MENU
// -----------------------------------------------

--dropdown-menu-bg-cl: var(--cl-white);
--dropdown-menu-shadow: var(--shadow-3);

// -----------------------------------------------
// MENU


// ITEM
// -----------------------------------------------

--dropdown-item-default-bg-cl: var(--cl-white);
--dropdown-item-hover-bg-cl: var(--ca-gray-600-50);

// -----------------------------------------------
// ITEM


// LINK
// -----------------------------------------------

--dropdown-link-cl: var(--cl-gray-100);

// -----------------------------------------------
// LINK


}
// stylelint-enable indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
