// ===============================================
// COLOR VARIABLES
// ===============================================


// stylelint-disable order/order,indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
:root[data-theme='asap'] {


// BASE COLORS
// -----------------------------------------------

$cl: (
	black: #000000,
	white: #FFFFFF,
	gray-100: #18181D,
	gray-200: #474A50,
	gray-300: #7A7D82,
	gray-400: #AEB0B4,
	gray-500: #C8C9CD,
	gray-600: #E1E2E6,
	gray-700: #F0F1F5,
	gray-800: #F8F8FB,
	graphite-100: #1E2228,
	graphite-200: #2D333D,
	graphite-300: #3B4451,
	green-100: #10A863,
	green-200: #25EB92,
	green-300: #67F0AA,
	green-400: #91F4C0,
	green-500: #B5F7D5,
	green-600: #D8F9E8,
	blue-100: #126ADF,
	blue-200: #1275F8,
	blue-300: #378CF9,
	blue-400: #68A5FC,
	blue-500: #91BEFD,
	blue-600: #B8D6FE,
	blue-700: #E1EEFE,
	violet-100: #4F2ED2,
	violet-200: #603AF8,
	violet-300: #7758F9,
	violet-400: #947AFD,
	violet-500: #AF9CFF,
	violet-600: #CABDFF,
	violet-700: #E4DEFE,
	red-100: #E91E46,
	red-200: #FD214D,
	red-300: #FD3F64,
	red-400: #FD5662,
	red-500: #FD6F7A,
	red-600: #FDA2A9,
	red-700: #FDD5D7,
	orange-100: #FF780A,
	orange-200: #FF943D,
	orange-300: #FFAB63,
	orange-400: #FFC089,
	orange-500: #FFD5B0,
	orange-600: #FFEAD8,
	yellow-100: #E0A100,
	yellow-200: #FFC93F,
	yellow-300: #FFD469,
	yellow-400: #FFDF8F,
	yellow-500: #FFEAB4,
	yellow-600: #FFF4D9,
);

@include get-colors($cl);

// -----------------------------------------------
// BASE COLORS


// TRANSPARENT COLORS
// -----------------------------------------------

@include
get-alpha(
	$cl,
	'gray-400',
	(
		20: .2,
		30: .3,
		40: .4,
		50: .5,
	)
);

@include
get-alpha(
	$cl,
	'gray-600',
	(
		50: .5,
	)
);

// -----------------------------------------------
// TRANSPARENT COLORS


// SHADOWS
// -----------------------------------------------

--shadow-1: 0 3px 4px rgba(6, 0, 15, .15);
--shadow-2:
	0 1px 2px rgba(0, 0, 0, .06),
	0 1px 3px rgba(0, 0, 0, .1);
--shadow-3:
	0 1px 3px 0 rgba(60, 64, 67, .2),
	0 1px 3px 1px rgba(60, 64, 67, .1);

// -----------------------------------------------
// SHADOWS


}
// stylelint-enable order/order, indentation, block-closing-brace-empty-line-before, scss/selector-nest-combinators
