// ===============================================
// GRID SYSTEM
// ===============================================


.row {
	--gutter-x: #{$columns-gutter-x-default};
	--gutter-y: 0;

	display: flex;
	flex-wrap: wrap;
	margin-bottom: calc(var(--gutter-y) * -1);
	// stylelint-disable-next-line order/properties-order
	margin-right: calc(var(--gutter-x) / -2);
	margin-left: calc(var(--gutter-x) / -2);

	> * {
		/*
			Prevent columns from becoming too narrow when at smaller grid tiers by
			always setting `width: 100%;`. This works because we set the width
			later on to override this initial width.
		*/
		flex-shrink: 0;
		width: 100%;
		// Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid
		max-width: 100%;
		margin-bottom: var(--gutter-y);
		padding-right: calc(var(--gutter-x) / 2);
		padding-left: calc(var(--gutter-x) / 2);
	}
}


@each $breakpoint in map-keys($grid-breakpoints) {
	$prefix: breakpoint-prefix($breakpoint, $grid-breakpoints);

	// stylelint-disable-next-line order/order
	@include media-breakpoint-up($breakpoint, $grid-breakpoints) {
		// Provide basic `.col-{bp}` classes for equal-width flexbox columns
		.#{$prefix}col {
			// Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4
			// stylelint-disable-next-line property-disallowed-list
			flex: 1 0 0%;
		}

		// stylelint-disable-next-line scss/selector-nest-combinators
		.#{$prefix}row-cols-auto > * {
			@include make-col-auto;
		}

		// stylelint-disable-next-line order/order
		@if $columns > 0 {
			@for $i from 1 through $columns {
				.#{$prefix}row-cols-#{$i} {
					@include row-cols($i);
				}
			}
		}

		.#{$prefix}col-auto {
			@include make-col-auto;
		}

		// stylelint-disable-next-line order/order
		@if $columns > 0 {
			@for $i from 1 through $columns {
				.#{$prefix}col-#{$i} {
					@include make-col($i, $columns);
				}
			}

			// `$columns - 1` because offsetting by the width of an entire row isn't possible
			@for $i from 0 through ($columns - 1) {
				// Avoid emitting useless .offset-0
				@if not ($prefix == '' and $i == 0) {
					.#{$prefix}offset-#{$i} {
						@include make-col-offset($i, $columns);
					}
				}
			}
		}
	}
}
