// ===============================================
// BUTTONS
// ===============================================


.btn {
	@include transition(background-color .2s, border-color .2s);

	--btn-py: var(--btn-default-py);
	--btn-px: var(--btn-default-px);
	--btn-cl: var(--btn-default-cl);
	--btn-fz: var(--btn-default-fz);
	--btn-bg-cl: var(--btn-default-bg-cl);
	--btn-brs: var(--btn-default-brs);
	--btn-border-cl: var(--btn-default-border-cl);

	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: var(--btn-py) var(--btn-px);
	border-width: 2px;
	border-style: solid;
	border-color: var(--btn-border-cl);
	border-radius: var(--btn-brs);
	background-color: var(--btn-bg-cl);
	color: var(--btn-cl);
	font-family: var(--btn-ff);
	font-size: var(--btn-fz);
	font-weight: var(--btn-fw);
	line-height: 1;
	text-align: center;
	text-decoration: none;
	white-space: nowrap;
	cursor: pointer;
	user-select: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;

	&--disabled,
	&:disabled {
		cursor: default;
	}

	&--primary {
		--btn-cl: var(--btn-primary-cl);
		--btn-bg-cl: var(--btn-primary-bg-cl);

		// stylelint-disable-next-line order/order
		&:focus {
			--btn-bg-cl: var(--btn-primary-hov-bg-cl);
		}

		@media (hover: hover) {
			&:hover {
				--btn-bg-cl: var(--btn-primary-hov-bg-cl);
			}
		}

		// stylelint-disable-next-line order/order
		&:active {
			--btn-bg-cl: var(--btn-primary-active-bg-cl);
		}

		&.btn--disabled,
		&:disabled {
			--btn-bg-cl: var(--btn-primary-disabled-bg-cl);
		}
	}

	&--ghost {
		--btn-cl: var(--btn-ghost-cl);
		--btn-bg-cl: var(--btn-ghost-bg-cl);
		--btn-border-cl: var(--btn-ghost-border-cl);

		// stylelint-disable-next-line order/order
		&:focus {
			--btn-bg-cl: var(--btn-ghost-hov-bg-cl);
		}

		@media (hover: hover) {
			&:hover {
				--btn-bg-cl: var(--btn-ghost-hov-bg-cl);
			}
		}

		// stylelint-disable-next-line order/order
		&:active {
			--btn-bg-cl: var(--btn-ghost-active-bg-cl);
		}

		&.btn--disabled,
		&:disabled {
			--btn-cl: var(--btn-ghost-disabled-cl);
			--btn-bg-cl: var(--btn-ghost-disabled-bg-cl);
		}
	}

	&--rounded {
		padding: 4px;
		border-radius: 50%;
	}
}

@each $breakpoint in map-keys($grid-breakpoints) {
	@include media-breakpoint-up($breakpoint) {
		$prefix: breakpoint-prefix($breakpoint, $grid-breakpoints);

		.#{$prefix}btn--sm {
			--btn-fz: var(--btn-sm-fz);
			--btn-py: var(--btn-sm-py);
			--btn-px: var(--btn-sm-px);
		}

		.#{$prefix}btn--lg {
			--btn-fz: var(--btn-lg-fz);
			--btn-py: var(--btn-lg-py);
			--btn-px: var(--btn-lg-px);
			--btn-brs: var(--btn-lg-brs);
		}
	}
}


// Future-proof disabling of clicks on `<a>` elements

// stylelint-disable selector-no-qualifying-type
a {
	&.btn {
		&--disabled {
			pointer-events: none;
		}
	}
}
// stylelint-enable selector-no-qualifying-type
