// ===============================================
// DROPDOWN
// ===============================================

.dropdown {
	--dropdown-toggle-bg-cl: var(--dropdown-toggle-default-bg-cl);
	--dropdown-item-bg-cl: var(--dropdown-item-default-bg-cl);

	position: relative;

	&__toggle {
		display: inline-flex;
		align-items: center;
		padding: 8px 12px;
		border: none;
		border-radius: 8px;
		background-color: var(--dropdown-toggle-bg-cl);
		color: var(--dropdown-toggle-cl);
		font-size: var(--dropdown-toggle-fz);
		font-weight: 700;
		text-decoration: none;
		cursor: pointer;
		transition: background-color .4s ease;

		&:hover,
		&:focus {
			--dropdown-toggle-bg-cl: var(--dropdown-toggle-hover-bg-cl);
		}

		&:active {
			--dropdown-toggle-bg-cl: var(--dropdown-toggle-active-bg-cl);
		}
	}

	&__icon {
		margin-left: 4px;
		font-size: 20px;
	}

	&__menu {
		position: absolute;
		top: calc(100% + 8px);
		left: 0;
		display: none;
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		border-radius: 8px;
		overflow: hidden;
		background-color: var(--dropdown-menu-bg-cl);
		box-shadow: var(--dropdown-menu-shadow);
		list-style-type: none;

		&--expanded {
			display: block;
		}
	}

	&__item {
		background-color: var(--dropdown-item-bg-cl);
		transition: background-color .2s ease;

		&:hover,
		&:focus {
			--dropdown-item-bg-cl: var(--dropdown-item-hover-bg-cl);
		}
	}

	&__link {
		display: flex;
		align-items: center;
		padding: 8px 16px 8px 12px;
		color: var(--dropdown-link-cl);
		font-size: var(--dropdown-link-fz);
		font-weight: 700;
		text-decoration: none;
	}
}
