// ===============================================
// HEADER
// ===============================================


.header {
	--header-link-cl: var(--header-link-default-cl);
	--header-link-fz: var(--df_header-link-fz);

	position: fixed;
	top: 0;
	left: 0;
	z-index: 20;
	flex-shrink: 0;
	width: 100%;
	background-color: var(--header-bg-cl);
	box-shadow: var(--header-shadow);

	&__container {
		@include media-breakpoint-down(md) {
			flex-wrap: wrap;
			padding-top: 12px;
			padding-bottom: 12px;
		}

		display: flex;
		align-items: center;
		padding-top: 20px;
		padding-bottom: 20px;
	}

	&__logo {
		@include media-breakpoint-down(md) {
			order: 1;
		}

		@include media-breakpoint-up(lg) {
			margin-right: 28px;
		}

		flex-shrink: 0;
		margin-right: 44px;
	}

	&__navigation {
		@include media-breakpoint-down(md) {
			display: none;
			order: 3;
			width: 100%;
			padding-top: 20px;
			padding-bottom: 16px;
		}

		display: flex;
		flex-grow: 1;
		align-items: center;
		justify-content: space-between;

		&--expanded {
			@include media-breakpoint-down(md) {
				display: block;
			}
		}
	}

	&__hr {
		@include media-breakpoint-down(md) {
			display: block;
		}

		display: none;
		margin-top: 16px;
		margin-bottom: 24px;
	}

	&__menu {
		@include media-breakpoint-down(md) {
			--menu-gap-y: 8px;
			--menu-gap-x: 0;

			grid-template-columns: 1fr 1fr;
		}

		@include media-breakpoint-up(lg) {
			--menu-gap-x: 28px;

			grid-auto-flow: column;
		}

		--menu-gap-x: 44px;
		--menu-gap-y: 0;

		display: grid;
		row-gap: var(--menu-gap-y);
		column-gap: var(--menu-gap-x);
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		list-style-type: none;

		&--actions {
			@include media-breakpoint-down(md) {
				--menu-gap-y: 20px;
				--menu-gap-x: 8px;

				padding-right: 8px;
				padding-left: 8px;
			}

			--menu-gap-x: 20px;
		}
	}

	&__link {
		@include media-breakpoint-down(lg) {
			--header-link-fz: var(--lg_header-link-fz);
		}

		@include media-breakpoint-down(md) {
			display: inline-block;
			padding: 8px;
			line-height: 1.2;
		}

		color: var(--header-link-cl);
		font-size: var(--header-link-fz);
		font-weight: 700;
		text-decoration: none;
		transition: color .2s ease;

		&:hover {
			--header-link-cl: var(--header-link-hover-cl);
		}
	}

	&__burger {
		@include media-breakpoint-down(md) {
			display: block;
			order: 2;
			margin-left: auto;
		}

		display: none;
	}
}
