// NUNITO SANS
// -----------------------------------------------

@font-face {
	font-family: 'Nunito Sans';
	font-weight: 700;
	font-style: normal;
	font-display: swap;
	src:
		url('../fonts/nunito-sans/NunitoSans-ExtraBold.woff2') format('woff2'),
		url('../fonts/nunito-sans/NunitoSans-ExtraBold.woff') format('woff');
}

@font-face {
	font-family: 'Nunito Sans';
	font-weight: 700;
	font-style: normal;
	font-display: swap;
	src:
		url('../fonts/nunito-sans/NunitoSans-Bold.woff2') format('woff2'),
		url('../fonts/nunito-sans/NunitoSans-Bold.woff') format('woff');
}

@font-face {
	font-family: 'Nunito Sans';
	font-weight: 400;
	font-style: normal;
	font-display: swap;
	src:
		url('../fonts/nunito-sans/NunitoSans-Regular.woff2') format('woff2'),
		url('../fonts/nunito-sans/NunitoSans-Regular.woff') format('woff');
}

@font-face {
	font-family: 'Nunito Sans';
	font-weight: 600;
	font-style: italic;
	font-display: swap;
	src:
		url('../fonts/nunito-sans/NunitoSans-SemiBoldItalic.woff2') format('woff2'),
		url('../fonts/nunito-sans/NunitoSans-SemiBoldItalic.woff') format('woff');
}

@font-face {
	font-family: 'Nunito Sans';
	font-weight: 600;
	font-style: normal;
	font-display: swap;
	src:
		url('../fonts/nunito-sans/NunitoSans-SemiBold.woff2') format('woff2'),
		url('../fonts/nunito-sans/NunitoSans-SemiBold.woff') format('woff');
}

