// ===============================================
// MAIN LAYOUT
// ===============================================


html,
body {
	height: 100%;
}

.root {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.main {
	@include media-breakpoint-up(lg) {
		margin-top: 76px;
	}

	flex-grow: 1;
	flex-shrink: 0;
	margin-top: 60px;
}

.container {
	width: 100%;
	max-width: 1280px;
	margin: 0 auto;
	padding-right: 16px;
	padding-left: 16px;
}

.section {
	@include media-breakpoint-up(lg) {
		padding-top: 80px;
		padding-bottom: 120px;
	}

	--section-bg-cl: var(--section-default-bg-cl);
	--section-cl: var(--section-default-cl);

	padding-top: 52px;
	padding-bottom: 64px;
	background-color: var(--section-bg-cl);
	color: var(--section-cl);

	&__heading {
		@include media-breakpoint-up(lg) {
			margin-bottom: 60px;
		}

		margin-bottom: 28px;
		text-align: center;
	}

	&__subheading {
		@include media-breakpoint-up(lg) {
			margin-top: 80px;
			margin-bottom: 60px;
		}

		margin-top: 44px;
		margin-bottom: 28px;
		text-align: center;
	}

	&--light {
		--section-bg-cl: var(--section-light-bg-cl);
	}

	&--dark {
		--section-bg-cl: var(--section-dark-bg-cl);
		--section-cl: var(--section-dark-cl);
	}
}


.hero {
	@include media-breakpoint-down(md) {
		padding-top: 0;
		padding-bottom: 56px;
	}

	padding-bottom: 0;
	overflow: hidden;

	&__picture {
		@include media-breakpoint-down(md) {
			display: none;
		}

		margin-top: auto;
	}

	&__img {
		display: block;
		width: 100%;
		border-top-left-radius: 8px;
		border-top-right-radius: 8px;
		box-shadow: 0 3px 4px rgba(6, 0, 15, .15);
	}

	&__content {
		@include media-breakpoint-up(xl) {
			padding-top: 70px;
		}

		padding-top: 56px;
	}
}

