// ===============================================
// IMAGE MIXINS
// ===============================================


/*
	Responsive images
	Keep images from scaling beyond the width of their parents.
*/

@mixin img-fluid {
	max-width: 100%;
	height: auto;
}


/*
	Generate classes for progressive image formats with fallback
	to supported image types.
*/

@mixin bg-img($path, $format) {
	background-image: url('#{$path}.webp');

	.no-webp & {
		background-image: url('#{$path}.#{$format}');
	}
}
