// ===============================================
// LINKS
// ===============================================


// Set default link style

.link {
	@include transition(color .2s ease);

	--link-cl: var(--link-default-cl);

	border: none;
	background-color: var(--link-bg);
	color: var(--link-cl);
	text-decoration: var(--link-decoration);

	&:hover,
	&:focus {
		--link-cl: var(--link-default-hover-cl);
	}

	&:active {
		--link-cl: var(--link-default-active-cl);
	}

	&--violet {
		--link-cl: var(--link-violet-cl);

		&:hover,
		&:focus {
			--link-cl: var(--link-violet-hover-cl);
		}

		&:active {
			--link-cl: var(--link-violet-active-cl);
		}
	}
}
