// ===============================================
// BLOCKS
// ===============================================


.logo {
	@include media-breakpoint-up(xl) {
		max-width: 152px;
	}

	display: block;
	width: 100%;
	max-width: 120px;
	height: auto;
}


.info {
	display: flex;
	padding: 12px 20px 12px 16px;
	border: 1px solid var(--info-border-cl);
	border-radius: 8px;
	background-color: var(--info-border-bg-cl);

	&__icon {
		flex-shrink: 0;
		margin-right: 10px;
		font-size: var(--info-icon-fz);
	}

	&__content {
		@include create-text-properties(text-md, md);

		line-height: 1.5;
	}
}


.video {
	@include media-breakpoint-up(sm) {
		border-radius: 12px;
	}

	display: block;
	width: 100%;
	height: auto;
	border-radius: 6px;
}


.feature {
	display: flex;

	&__icon {
		@include media-breakpoint-up(sm) {
			margin-right: 28px;
		}

		flex-shrink: 0;
		margin-right: 24px;
	}

	&__heading {
		margin-top: 2px;
		margin-bottom: 0;
	}

	&__description {
		margin-top: 4px;
	}
}


.doc-example {
	--doc-example-img-border-cl: var(--doc-example-img-default-border-cl);

	display: block;

	&:hover {
		.doc-example__img {
			--doc-example-img-border-cl: var(--doc-example-img-hover-border-cl);
		}
	}

	&__img {
		display: block;
		max-width: 100%;
		height: auto;
		border: 1px solid var(--doc-example-img-border-cl);
		border-radius: 8px;
		transition: border-color .2s ease;
	}
}
